<script>
  import { crossfade, scale } from "svelte/transition";
  import { flip } from "svelte/animate";

  import { games, gameActivities } from "../../stores";
  import Spinner from "../layout/Spinner.svelte";

  const DURATION = 350;

  const [send, receive] = crossfade({
    duration: DURATION,
    fallback: scale
  });

  // const [send, receive] = crossfade({
  //   fallback(node, params) {
  //     const style = getComputedStyle(node);
  //     const transform = style.transform === "none" ? "" : style.transform;

  //     return {
  //       duration: 600,
  //       easing: quintOut,
  //       css: t => `
  // 				transform: ${transform} scale(${t});
  // 				opacity: ${t}
  // 			`
  //     };
  //   }
  // });

  let activeFilter;
  let selected;

  function setFilter(filter) {
    if (filter === activeFilter) {
      activeFilter = undefined;
      return;
    }
    activeFilter = filter;
  }
</script>

<style>
  .grid {
    display: grid;
    grid-gap: 1.25rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: flex-end;
  }
  @media screen and (min-width: 769px) {
    .grid {
      grid-template-columns: repeat(5, 1fr);
    }
  }
  .overview {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    will-change: transform;
  }
</style>

<div class="filters">
  <button
    class="button"
    class:is-primary={activeFilter === 'Purchased'}
    on:click={() => setFilter('Purchased')}>
    Purchased
  </button>
  <button
    class="button"
    class:is-primary={activeFilter === 'Started'}
    on:click={() => setFilter('Started')}>
    Started
  </button>
  <button
    class="button"
    class:is-primary={activeFilter === 'Beaten'}
    on:click={() => setFilter('Beaten')}>
    Beaten
  </button>
  <button
    class="button"
    class:is-primary={activeFilter === 'Completed'}
    on:click={() => setFilter('Completed')}>
    Completed
  </button>
</div>

<div class="content">
  <h1 class="title">

    {#if activeFilter}
      Games that have been
      <span class="has-text-primary">{activeFilter}</span>
    {:else}All Games{/if}
  </h1>
  <!-- {#if $games.loading}
    <Spinner />
  {:else}
    <ul>
      {#each $games.items as { name }}
        <li>{name}</li>
      {/each}
    </ul>
  {/if} -->
  {#if $gameActivities.loading}
    <Spinner />
  {:else}
    <div class="grid">
      {#each $gameActivities.items.filter(item => !activeFilter || item.status.name === activeFilter) as { game, status } (game.id)}
        <div class="cell" animate:flip={{ duration: DURATION }}>
          {#if selected !== game}
            <img
              src={game.cover}
              alt={game.name}
              on:click={() => (selected = game)}
              in:receive={{ key: game.id }}
              out:send={{ key: game.id }} />
          {/if}
        </div>
      {/each}
    </div>

    {#if selected}
      {#await selected then d}
        <div
          class="overview"
          on:click={() => (selected = null)}
          in:receive={{ key: d.id }}
          out:send={{ key: d.id }}>
          <img src={d.cover} alt={d.name} />
          <h2>{d.name}</h2>
        </div>
      {/await}
    {/if}
  {/if}
</div>
