import {
  writable,
  derived,
  get
} from 'svelte/store';
import {
  post
} from '../utils';

const AUTH_TOKEN_KEY = 'auth_token_key';

function createAuth() {
  const defaultAuth = {
    user: null,
    token: '',
    refreshToken: ''
  }

  const storedToken = JSON.parse(sessionStorage.getItem(AUTH_TOKEN_KEY));

  if (storedToken) {
    refreshToken();
  }

  const {
    subscribe,
    set
  } = writable(storedToken || defaultAuth);

  return {
    subscribe,
    login: (user, tokens) => {
      sessionStorage.setItem(AUTH_TOKEN_KEY, JSON.stringify({
        user,
        token: tokens.access_token,
        refreshToken: tokens.refresh_token
      }));

      set({
        user,
        token: tokens.access_token,
        refreshToken: tokens.refresh_token
      });
      refreshToken(tokens.expires_in);
    },
    logout: () => {
      sessionStorage.removeItem(AUTH_TOKEN_KEY);
      set(defaultAuth);
    }
  };
}

let refreshInterval;
export const auth = createAuth();
export const isLoggedIn = derived(auth, ({
  token
}) => Boolean(token));

function refreshToken(delay) {
  if (!delay) {
    delay = 30 * 1000;
  } else {
    // Ensure the token is refreshed before it expires
    delay -= (30 * 1000);
  }
  if (refreshInterval) {
    clearInterval(refreshInterval);
  }

  refreshInterval = setInterval(async () => {
    const tokens = get(auth);
    if (!tokens.token && refreshInterval) {
      clearInterval(refreshInterval);
      return;
    }
    const result = await post('/auth/refresh', {
      refresh_token: tokens.refreshToken
    }, tokens.token);
    auth.login(tokens.user, result);
  }, delay);
}