import {
  get as getStore
} from 'svelte/store';

import {
  get,
  post
} from '../utils/ajax';
import {
  auth
} from './Auth';
import {
  cacheResults,
  cacheBuster
} from './cache';

export function createEntityStore(endpoint) {
  const buster = cacheBuster();

  const {
    subscribe
  } = cacheResults((token) => get(endpoint, token), buster);

  return {
    subscribe,
    create: async (data) => {
      const result = await post(
        endpoint, data,
        getStore(auth).token
      );

      buster.clear();
      return result;
    },
  }
}